

import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'radio2',
created(){
let options = []
let selected = ""
let value = this.attrs.value
for (let i = 0; i < value.length; i++) {
if (value[i].selected) {
selected = value[i].text
}
options.push(value[i].text)
}

this.options=options
this.value=selected
},
props: {
attrs: {
type: Object,
default() {
return {}
}
}
},
components:{
Radio,Group
}
}




